@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');
/* –ó–∞–≥–∞–ª—å–Ω—ñ —Å—Ç–∏–ª—ñ –¥–ª—è header */
.header {
    top: 0;
    left: 0;
    width: 100%;
    padding: 20px 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    background: white;
}

/* –°—Ç–∏–ª—å –¥–ª—è –ª–æ–≥–æ—Ç–∏–ø–∞ */
.logo {
    display: flex;
    align-items: center;
    font-size: 32px;
    color: pink;
    font-weight: 700;
    text-decoration: none;
}

.logo-img {
    width: 50px;
    height: auto;
    margin-right: 10px;
    border-radius: 15px;
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –Ω–∞–≤—ñ–≥–∞—Ü—ñ–π–Ω–æ–≥–æ –º–µ–Ω—é */
.navbar {
    display: flex;
    align-items: center;
}
* {
    font-family: 'Nunito', sans-serif;
}
/* –°—Ç–∏–ª—ñ –¥–ª—è –ø–æ—Å–∏–ª–∞–Ω—å –≤ –Ω–∞–≤—ñ–≥–∞—Ü—ñ–π–Ω–æ–º—É –º–µ–Ω—é */
.navbar a {
    position: relative;
    font-size: 18px;
    color: pink;
    font-weight: 700;
    margin-left: 20px;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
}


.navbar a::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    width: 0;
    height: 2px;
    background: pink;
    transition: .3s;
}

.navbar a:hover::before {
    width: 100%;
}

/* –°—Ç–∏–ª—ñ –¥–ª—è –±—É—Ä–≥–µ—Ä-–º–µ–Ω—é */
.burger-menu {
    display: none; /* –ü—Ä–∏—Ö–æ–≤—É—î–º–æ –±—É—Ä–≥–µ—Ä-–º–µ–Ω—é –∑–∞ –∑–∞–º–æ–≤—á—É–≤–∞–Ω–Ω—è–º */
    cursor: pointer;
    z-index: 101;
}

.line {
    width: 30px;
    height: 4px;
    background-color: pink;
    margin: 6px 0;
    transition: 0.3s;
}

/* –ú–µ–¥—ñ–∞-–∑–∞–ø–∏—Ç–∏ –¥–ª—è –∞–¥–∞–ø—Ç–∞—Ü—ñ—ó */
@media (max-width: 1200px) {
    .header {
        padding: 20px 5%;
    }

    .navbar {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    .navbar a {
        font-size: 16px;
        margin-left: 15px;
    }

    .logo {
        font-size: 28px;
    }
}

@media (max-width: 768px) {
    .header {
        padding: 20px 5%;
    }

    /* –ü—Ä–∏—Ö–æ–≤—É—î–º–æ –º–µ–Ω—é –Ω–∞ –º–æ–±—ñ–ª—å–Ω–∏—Ö */
    .navbar {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 88px;
        right: 0;
        background: rgba(0, 0, 0, 0.8);
        width: 100%;
        text-align: center;
    }

    /* –í—ñ–¥–æ–±—Ä–∞–∂–∞—î–º–æ –º–µ–Ω—é, –∫–æ–ª–∏ –¥–æ–¥–∞–Ω–æ –∫–ª–∞—Å active */
    .navbar.active {
        display: flex;
    }

    .navbar a {
        margin: 15px 0;
        font-size: 20px;
    }

    .burger-menu {
        display: block; /* –ü–æ–∫–∞–∑—É—î–º–æ –±—É—Ä–≥–µ—Ä-–º–µ–Ω—é –ø—Ä–∏ –º–∞–ª–∏—Ö –µ–∫—Ä–∞–Ω–∞—Ö */
    }
}

@media (max-width: 480px) {
    .logo {
        font-size: 24px;
    }

    .navbar a {
        font-size: 18px;
        margin-left: 10px;
    }

    .burger-menu .line {
        width: 25px;
    }
}
