/* –î–ª—è –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ –∫–∞—Ä—Ç–æ–∫ */
.card-container {
    display: flex;
    justify-content: center; /* –¶–µ–Ω—Ç—Ä—É—î–º–æ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—ñ */
    align-items: center; /* –¶–µ–Ω—Ç—Ä—É—î–º–æ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª—ñ */
    gap: 80px;
    flex-wrap: wrap;
    padding-top: 25px;
    width: 100%; /* –í–∏—Å–æ—Ç–∞ –µ–∫—Ä–∞–Ω—É –º—ñ–Ω—É—Å 150px –¥–ª—è –≤—ñ–¥—Å—Ç—É–ø—É */
  }
  
  /* –î–ª—è –∫–æ–∂–Ω–æ—ó –∫–∞—Ä—Ç–∫–∏ */
  .card {
    max-width: 300px;
    border-radius: 0.5rem;
    background-color: #fff;
    box-shadow: 0 5px 2px  rgba(0, 0, 0, 0.05);
    border: 1px solid transparent;
    background-color: rgba(255, 255, 255, 0.6); /* –ø—Ä–æ–∑–æ—Ä–∏–π –±—ñ–ª–∏–π —Ñ–æ–Ω */
  }
  
  .card a {
    text-decoration: none;
  }
  
  .content {
    padding: 1.1rem;
  }
  
  .image {
    object-fit: cover;
    width: 100%;
    height: 250px;
    background-size: cover;  /* –ó–∞–±–µ–∑–ø–µ—á—É—î, —â–æ–± –∫–∞—Ä—Ç–∏–Ω–∫–∞ –∑–∞–ø–æ–≤–Ω—é–≤–∞–ª–∞ –±–ª–æ–∫ –±–µ–∑ –¥–µ—Ñ–æ—Ä–º–∞—Ü—ñ—ó */
    background-position: center; /* –¶–µ–Ω—Ç—Ä—É—î –∫–∞—Ä—Ç–∏–Ω–∫—É –≤ –º–µ–∂–∞—Ö –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
    border-radius: 5px;
  }
  
  .title {
    color: #111827;
    font-size: 1.125rem;
    line-height: 1.75rem;
    font-weight: 600;
  }
  
  .desc {
    margin-top: 0.5rem;
    color: #6B7280;
    font-size: 0.875rem;
    line-height: 1.25rem;
    text-align: justify;
    text-indent: 20px;
  }
  
  .action {
    display: inline-flex;
    margin-top: 1rem;
    color: #ffffff;
    font-size: 0.875rem;
    line-height: 1.25rem;
    font-weight: 500;
    align-items: center;
    gap: 0.25rem;
    background-color: rgb(243, 102, 132);
    padding: 4px 8px;
    border-radius: 4px;
  }
  
  .action span {
    transition: .3s ease;
  }
  
  .action:hover span {
    transform: translateX(4px);
  }
  
.container-tite{
  color: #fff;
  text-align: center;
  margin-top: 50px;
  padding-bottom: 30px;
  font-weight: 900;
  font-size: 30px;
  width: 100%;

}